// @flow strict

import {useCallback, useEffect, useState} from 'react';
import useStable from './useStable';

export type ColorMode = 'dark' | 'light';

const COLOR_MODE_LOCAL_STORAGE_KEY = 'preferredColorMode';

function getColorModeFromString(s: ?string): ColorMode | null {
  if (s === 'light') {
    return s;
  }
  if (s === 'dark') {
    return s;
  }
  return null;
}

export default function useColorMode(
  defaultColorMode: ColorMode,
): {
  mode: ColorMode,
  toggleColorMode: () => void,
} {
  // It's probably not necessary to check both of these.
  // It'd really only apply in a case where the browser/OS didn't specify either.
  // In the future I may want to listen for changes to this.
  const prefersDarkModeMQ = useStable(
    () =>
      window.matchMedia != null &&
      window.matchMedia('(prefers-color-scheme: dark)').matches,
  );
  const prefersLightModeMQ = useStable(
    () =>
      window.matchMedia != null &&
      window.matchMedia('(prefers-color-scheme: light)').matches,
  );
  // eslint-disable-next-line no-nested-ternary
  const preferredModeFromMediaQuery: ColorMode | null = prefersDarkModeMQ
    ? 'dark'
    : prefersLightModeMQ
    ? 'light'
    : null;

  const storedPreferredColorModeString = window.localStorage.getItem(
    COLOR_MODE_LOCAL_STORAGE_KEY,
  );
  const storedPreferredColorMode: ColorMode | null = getColorModeFromString(
    storedPreferredColorModeString,
  );

  // Color should be determined by explicit setting > media query > default;
  const [mode, setMode] = useState<ColorMode>(
    storedPreferredColorMode ?? preferredModeFromMediaQuery ?? defaultColorMode,
  );
  const [
    didExplicitlySetColorMode,
    setDidExplicitlySetColorMode,
  ] = useState<boolean>(false);

  useEffect(() => {
    if (didExplicitlySetColorMode) {
      window.localStorage.setItem(COLOR_MODE_LOCAL_STORAGE_KEY, mode);
    }
  }, [didExplicitlySetColorMode, mode]);

  const toggleColorMode = useCallback(() => {
    setDidExplicitlySetColorMode(true);
    setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
  }, []);

  return {mode, toggleColorMode};
}
